@import url(https://fonts.googleapis.com/css?family=Montserrat|Raleway&display=swap);
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

* {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3 {
  font-family: 'Montserrat', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.container {
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: flex-start;
  align-content: flex-start;
  flex-direction: column;
  position: relative;
}

.section-container {
  background-image: -webkit-gradient(linear, left top, right bottom, from(rgba(29, 41, 81, 0.85)), to(rgba(29, 41, 81, 0.9))),
    url('https://res.cloudinary.com/dsira9sgt/image/upload/v1571375265/Daniel%20Garcia/fabian-grohs-dC6Pb2JdAqs-unsplash_1_vuk0yf.jpg');
  background-image: -webkit-linear-gradient(left top, rgba(29, 41, 81, 0.85), rgba(29, 41, 81, 0.9)),
    url('https://res.cloudinary.com/dsira9sgt/image/upload/v1571375265/Daniel%20Garcia/fabian-grohs-dC6Pb2JdAqs-unsplash_1_vuk0yf.jpg');
  background-image: linear-gradient(to right bottom, rgba(29, 41, 81, 0.85), rgba(29, 41, 81, 0.9)),
    url('https://res.cloudinary.com/dsira9sgt/image/upload/v1571375265/Daniel%20Garcia/fabian-grohs-dC6Pb2JdAqs-unsplash_1_vuk0yf.jpg');
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-size: cover;
  background-repeat: no-repeat;
}

/* Transitions */
.section-enter {
  opacity: 0.1;
}
.section-enter-active {
  opacity: 1;
  -webkit-transition: opacity 250ms;
  transition: opacity 250ms;
}
.section-exit {
  opacity: 1;
}
.section-exit-active {
  opacity: 0.1;
  -webkit-transition: opacity 250ms;
  transition: opacity 250ms;
}

.home {
  color: #fff;
  justify-content: center;
  align-items: center;
}

.speach-container {
  width: 55vw;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.speach-container .text {
  width: 40vw;
}

.speach-container h1 {
  margin: 0;
}

.speach-container h2 {
  margin-top: 0;
  font-weight: normal;
  font-size: 2.4rem;
}

.avatar {
  width: 12vw;
  height: 12vw;
  border-radius: 100%;
  overflow: hidden;
  margin-right: 3vw;
}

.avatar > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.speach-container p {
  margin-bottom: 5rem;
}

.speach-container a {
  color: inherit;
  text-decoration: none;
  border-radius: 50px;
  border: 2px solid #fff;
  padding: 1rem 2rem;
  font-size: 1.6rem;
  margin-top: 3rem;
}

.navbar {
  height: 10vh;
  width: 100vw;
  position: fixed;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 2rem;
  color: #fff;
}

.navbar > ul {
  list-style: none;
  display: flex;
}

.navbar > ul > li {
  margin: 0 1rem;
}

.navbar a {
  color: inherit;
  text-decoration: none;
  font-size: 1.8rem;
  padding: 1rem 0.5rem;
}

.navbar .active {
  /* border-bottom: 3px solid rgb(15, 77, 146); */
  font-weight: bold;
}

.social svg {
  font-size: 2.5rem;
}

.contact {
  color: #fff;
  justify-content: center;
  align-items: center;
}

.contact > form {
  display: flex;
  flex-direction: column;
  width: 50%;
  justify-content: center;
  align-items: center;
}

.contact > div {
  width: 50%;
}

.contact label {
  width: 100%;
  margin-bottom: 0.5rem;
}

.contact > form > div {
  width: 100%;
}

.contact input,
.contact textarea {
  border: none;
  background: none;
  background-color: #f1f3f4;
  padding: 1rem;
  width: 60%;
  margin-bottom: 2rem;
  border-radius: 5px;
  height: 3.5rem;
}

.contact textarea {
  width: 100%;
  height: 20rem;
}

.contact input[type='submit'] {
  width: 10rem;
  cursor: pointer;
}

.projects {
  justify-content: center;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}

.project-container {
  width: 40rem;
  max-width: 90vw;
  padding: 3rem 3rem;
  border-radius: 8px;
  box-shadow: 0px 0px 6px 1px rgba(0, 0, 0, 0.4);
  background-color: #fff;
  height: 48rem;
  margin: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.8rem;
  text-decoration: none;
  color: inherit;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.project-container:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.project-container h3 {
  width: 100%;
  margin-bottom: 0.5rem;
  margin-top: 1rem;
}

.project-container ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  color: #fff;
  padding: 0;
  margin-top: 3rem;
}

.project-container li {
  padding: 0.5rem 1rem;
  margin: 0 1rem 2rem 0;
  background-color: rgb(14, 77, 146);
  border-radius: 50px;
}

.project-image {
  width: 90%;
  overflow: hidden;
  border-radius: 5px;
}

.project-image > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

